import React from 'react';

function Where({whereAnswer, setWhereAnswer}) {
  return (
    <div className="questions-container">
    <h1>And do you remember where exactly?</h1>
    <div className="button-container where">
      <button className={whereAnswer === 'yup' ? "standar-button selected" : "standar-button"} onClick={() => setWhereAnswer('yup')}>Location</button>
    </div>
  </div>
  )
}

export default Where;